import React from 'react';
import MediaQuery from 'react-responsive';
import styled from 'styled-components';
import { withTranslation } from 'react-i18next';
import i18next from 'i18next';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import {
    Layout,
    Button,
    Menu,
    Drawer,
    Divider,
} from 'antd';

import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    ProjectOutlined,
    UserOutlined,
    TwitterOutlined,
    GithubOutlined,
    MailOutlined,
    WalletOutlined,
    MedicineBoxOutlined,
    BankOutlined,
} from '@ant-design/icons';
import {
    ellipseAddress,
} from "../helpers/utilities";
import BalanceInfo from './BalanceInfo';
import MessageModal from './MessageModal';

import LogoImg from '../logo.png';
import Mining from '../images/mining.svg';
import Telegram from '../images/telegram.svg';

const { Sider } = Layout;
const { SubMenu } = Menu;


const SideMenuHeader = styled.header`
  background: #121212;
  padding: 0;
  color: #FF6666;
  height: 56px;
  width: 100%;
  padding: 0 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const Header = styled(SideMenuHeader)`
  top: 0;
  left: auto;
  right: 0;
  position: fixed;
  z-index: 1000;
  justify-content: space-between;
`;

const Logo = styled.img`
  height: 40px;
  width: 40px;
`;

const Content = styled.div``;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
`;

const ConnectedStatus = styled.div`
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #7FD1B9;
  margin-left: 5px;
  border-radius: 5px;
`;

const DisconnectedStatus = styled(ConnectedStatus)`
  background: #FF6666;
`;

const LanguageButton = styled.div`
    cursor: pointer;
    color: #FF6666;
    display: inline-block;
`;


interface ICusMenuProps {
    walletConnected: boolean;
    walletAddress: string;
    clkBalance: number;
    clkEarnings: number;
    handleConnect(): void;
    harvest(): void;
    t: any;
    i18n: any;
}

interface ICusMenuState {
    collapsed: boolean,
    drawerVisible: boolean,
    messageVisible: boolean,
    messageContent: any
    messageTitle: any,
    pending: boolean,
}

class CusMenu extends React.Component<ICusMenuProps, ICusMenuState> {
    constructor(props: ICusMenuProps) {
        super(props);

        this.state = {
            collapsed: false,
            drawerVisible: false,
            messageVisible: false,
            messageContent: '',
            messageTitle: '',
            pending: false
        };
    }

    showDrawer = () => {
        this.setState({
            drawerVisible: true,
        });
    }

    closeDrawer = () => {
        this.setState({
            drawerVisible: false,
        });
    }

    showMessage = (message: any, title?: any) => {
        this.setState({
            drawerVisible: false,
            messageVisible: true,
            messageContent: message,
            messageTitle: title,
        });
    }

    showIntro = () => {
        this.showMessage(
            'CloudLink is a decentralized community autonomous organization built on the Huobi ecosystem chain HECO. Its purpose is to introduce DOT to DEFI and accelerate the use of various products of Boca on DEFI. CLK is its governance token, with the right to submit proposals, vote, community benefit distribution, etc.',
            'Introduction'
        );
    };

    handleHarvest = async () => {
        await this.setState((state, props) => ({ pending: true }));
        try {
            await this.props.harvest();
        } finally {
            await this.setState((state, props) => ({ pending: false }));
        }
    }

    render() {
        const menu = <>
            <Menu
                theme="dark"
                mode="inline"
                defaultOpenKeys={['sub']}
                style={{ padding: "0", fontWeight: 500, fontSize: '1rem' }}
            >
                <SideMenuHeader>
                    <Logo src={LogoImg}></Logo>
                    <div>
                        <LanguageButton onClick={() => { i18next.changeLanguage('zh'); }}>中</LanguageButton> / <LanguageButton onClick={() => { i18next.changeLanguage('en'); }}>EN</LanguageButton>
                    </div>
                </SideMenuHeader>
                <Content style={{ background: "#121212", color: "#FFFFFF", border: "none", width: "100%", lineHeight: 1.5 }}>
                    <Button style={{ background: "#121212", color: "#FF6666", border: "none", width: "100%", padding: "10px 15px", height: "auto", fontSize: "14px" }} onClick={this.props.handleConnect}>
                        <WalletOutlined />
                        {this.props.walletConnected ? ellipseAddress(this.props.walletAddress, 6) : this.props.t('connect to wallet')}
                        {this.props.walletConnected ? <ConnectedStatus /> : <DisconnectedStatus />}
                    </Button>
                    <Content style={{ padding: "10px 15px" }}>
                        <BalanceInfo name={this.props.t('balance')} amount={this.props.clkBalance} unit="CLK"></BalanceInfo>
                        <BalanceInfo name={this.props.t('earnings')} amount={this.props.clkEarnings} unit="CLK"></BalanceInfo>
                        <Button
                            style={{ background: "#FF6666", color: "#FFFFFF", border: "none", width: "100%", padding: "6px 15px", height: "auto", fontSize: "1rem", marginTop: "6px" }}
                            onClick={this.handleHarvest}
                            loading={this.state.pending}>
                            {this.props.t('harvest')}
                        </Button>
                    </Content>
                </Content>
                <Divider style={{ background: "#e0e0e0", margin: "8px 0" }}></Divider>

                <Menu.Item style={{ color: "#FF6666" }} key="1" icon={<ProjectOutlined />} onClick={this.closeDrawer}>
                    <Link to="/cloudlink"><span style={{ color: "#FF6666" }}>{this.props.t('cloudLink')}</span></Link>
                </Menu.Item>
                <Menu.Item style={{ color: "#FF6666" }} key="11" icon={<BankOutlined />} onClick={this.closeDrawer}>
                    <Link to="/airdrop"><span style={{ color: "#FF6666" }}>{this.props.t('airdrop')}</span></Link>
                </Menu.Item>
                <Menu.Item style={{ color: "#FF6666" }} key="2" icon={<img src={Mining} style={{ width: "14px", height: "14px", fill: "#FF6666", }}></img>} onClick={this.closeDrawer}>
                    <Link to="/"><span style={{ color: "#FF6666" }}>{this.props.t('mining')}</span></Link>
                </Menu.Item>
                <SubMenu key="sub" title={this.props.t('audit report')} icon={<MedicineBoxOutlined />} style={{ color: "#FF6666" }}>
                    <Menu.Item key="3" onClick={this.closeDrawer}>
                        <Link to="/audit"><span style={{ color: "#FF6666" }}>{this.props.t('audit report1')}</span></Link>
                    </Menu.Item>
                    <Menu.Item key="4" onClick={() => {
                        this.showMessage(
                            this.props.t('auditing'),
                            this.props.t('audit report2'));
                    }}>
                        <span style={{ color: "#FF6666" }}>{this.props.t('audit report2')}</span>
                    </Menu.Item>
                </SubMenu>
                <Divider style={{ background: "#e0e0e0", margin: "8px 0" }}></Divider>
                <Menu.Item style={{ color: "#FF6666" }} key="5" icon={<TwitterOutlined />}><a href="http://twitter.com/CloudLink7"><span style={{ color: "#FF6666" }}>Twitter</span></a></Menu.Item>
                <Menu.Item style={{ color: "#FF6666" }} key="6" icon={<img src={Telegram} style={{ width: "15px", height: "15px" }}></img>}><a href="https://t.me/CloudLink1"><span style={{ color: "#FF6666" }}>Telegram</span></a></Menu.Item>
                <Menu.Item style={{ color: "#FF6666" }} key="7" icon={<GithubOutlined />}><a href="https://github.com/CloudLinkk"><span style={{ color: "#FF6666" }}>Github</span></a></Menu.Item>
                <Menu.Item style={{ color: "#FF6666" }} key="8" icon={<MailOutlined />} onClick={() => {
                    this.showMessage(
                        <p>supports@clouds-link.com</p>,
                        'Supports Email');
                }}><span style={{ color: "#FF6666" }}>Email</span></Menu.Item>
            </Menu>
        </>;

        return (
            <>
                <MediaQuery minWidth={1024}>
                    {/* <Sider trigger={null} collapsible collapsed={this.state.collapsed} style={{
                        background: "#121212",
                        overflow: 'auto',
                        height: '100vh',
                        position: 'fixed',
                        left: 0,
                    }}>
                        {menu}
                    </Sider> */}
                    <Drawer
                        // title={<Logo src={LogoImg} />}
                        placement="left"
                        closable={false}
                        onClose={this.closeDrawer}
                        visible={true}
                        mask={false}
                        key="left"
                        style={{ padding: "0", width: "300px"}}
                        footer={<div style={{ color: "#FFFFFF" }}>{this.props.t('version')}: 1.0.0</div>}
                    >
                        {menu}
                    </Drawer>
                </MediaQuery>

                <MediaQuery maxWidth={1024}>
                    <Header>
                        <Container>
                            <Logo src={LogoImg}></Logo>
                        </Container>

                        <div style={{display: "flex", alignItems: "center"}}>
                            <div style={{marginRight: "1rem"}}><LanguageButton onClick={() => { i18next.changeLanguage('zh'); }}>中</LanguageButton> / <LanguageButton onClick={() => { i18next.changeLanguage('en'); }}>EN</LanguageButton></div>
                            <Button
                                style={{ background: "transparent", border: "none" }}
                                onClick={this.showDrawer}>
                                {!this.state.drawerVisible ? <MenuFoldOutlined style={{ color: "#FF6666", fontSize: "24px" }} color="black" /> : <MenuUnfoldOutlined style={{ color: "#FF6666", fontSize: "24px" }} color="black" />}
                            </Button>
                        </div>
                    </Header>
                    <Drawer
                        // title={<Logo src={LogoImg} />}
                        placement="left"
                        closable={false}
                        onClose={this.closeDrawer}
                        visible={this.state.drawerVisible}
                        key="left"
                        style={{ padding: "0" }}
                        footer={<div style={{ color: "#FFFFFF" }}>{this.props.t('version')}: 1.0.0</div>}
                    >
                        {menu}
                    </Drawer>
                </MediaQuery>

                <MessageModal
                    visible={this.state.messageVisible}
                    title={this.state.messageTitle}
                    content={this.state.messageContent}
                    onClose={() => { this.setState({ messageVisible: false, messageTitle: null, messageContent: null }) }}>
                </MessageModal>
            </>
        );
    }
}

// export default CusMenu;

export default withTranslation()(CusMenu);