import React from 'react';
import { withTranslation } from 'react-i18next';
import LogoImg from './logo.png';
import './App.less';

import { Layout } from 'antd';
import styled from 'styled-components';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import Fortmatic from "fortmatic";
import Torus from "@toruslabs/torus-embed";
import { IPoolAssetData } from "./helpers/types";
import { getTokensPrice } from "./helpers/api";
import { getChainData } from "./helpers/utilities";

import OperationModal from './components/OperationModal';
import CusMenu from './components/CusMenu';
import Title from './components/Title';
import TotalValue from './components/TotalValue';
import StakeList from './components/StakeList';

import { PoolContract, TokenContract, AirdropContract } from './helpers/web3';
import { configuration } from './config';
import CloudLink from './pages/CloudLink';
import AirDrop from './pages/AirDrop';
import AuditReport from './pages/AuditReport';

const Content = styled.div``;

interface IAccount {
  clkBalance: number;
  earnings: number;
  totalValueLocked: number;
  portfolio: number;
}

interface IMessage {
  messageVisible: boolean;
  messageContent: any;
  messageTitle?: any;
}

interface IWallet {
  address: string;
  web3: any;
  provider: any;
  connected: boolean;
  chainId: number;
  networkId: number;
}

interface IModal {
  showModal: boolean;
  modalTitle: string;
  modalLogo: any;
  pool: IPoolAssetData,
  available: number;
  deposited: number;
  earnings: number;
  allowance: number;
}

interface IAppState {
  wallet: IWallet;
  poolAssets: { [address: string]: IPoolAssetData };
  modal: IModal;
  collapsed: boolean;
  drawerVisible: boolean;
  message: IMessage;
  initializing: boolean;
  account: IAccount;
  priceMap: { [name: string]: number };
  contractMap: { [name: string]: TokenContract & PoolContract };
}

const INITIAL_STATE: IAppState = {
  wallet: {
    address: "",
    web3: null,
    provider: null,
    connected: false,
    chainId: 128,
    networkId: 128,
  },
  poolAssets: {},
  modal: {
    showModal: false,
    modalTitle: '',
    modalLogo: null,
    available: 0,
    deposited: 0,
    earnings: 0,
    allowance: 0,
    pool: {
      symbol: '',
      name: '',
      logo: '',
      type: '',
      token0Address: '',
      token1Address: '',
      lpAddress: '',
      poolAddress: '',
      totalLockCount: 0,
      totalValueLocked: 0,
      apy: 0,
      myLocked: 0,
      earnings: 0,
      startTime: '',
      endTime: '',
      rewardAmount: 0,
      rewardPerDay: 0,
    },
  },
  collapsed: false,
  drawerVisible: false,
  message: {
    messageVisible: false,
    messageTitle: null,
    messageContent: null,
  },
  initializing: true,
  priceMap: {},
  contractMap: {},
  account: {
    clkBalance: 0,
    earnings: 0,
    totalValueLocked: 0,
    portfolio: 0,
  }
};

function initWeb3(provider: any) {
  const web3: any = new Web3(provider);

  web3.eth.extend({
    methods: [
      {
        name: "chainId",
        call: "eth_chainId",
        outputFormatter: web3.utils.hexToNumber
      }
    ]
  });

  return web3;
}

interface IAppProp {
  t: any;
  i18n: any;
}
class App extends React.Component<IAppProp, IAppState> {
  web3Modal: Web3Modal;
  state: IAppState;
  dataTimer: any;
  modalTimer: any;

  constructor(props: IAppProp) {
    super(props);
    this.state = {
      ...INITIAL_STATE
    };

    this.web3Modal = new Web3Modal({
      network: this.getNetwork(),
      cacheProvider: true,
      providerOptions: this.getProviderOptions()
    });
  }

  public componentDidMount() {
    if (this.web3Modal.cachedProvider || this.state.wallet.web3 == null) {
      this.onConnect();
    }
  }

  // componentWillUnmount() {
  //   clearInterval(this.dataTimer);
  // }
  componentWillUnmount() {
    clearInterval(this.dataTimer);
  }

  public onConnect = async () => {
    const provider = await this.web3Modal.connect();
    await this.subscribeProvider(provider);
    const web3: any = initWeb3(provider);
    const accounts = await web3.eth.getAccounts();
    const address = accounts[0];
    const networkId = await web3.eth.net.getId();
    const chainId = await web3.eth.chainId();
    await this.setState({
      wallet: {
        web3,
        provider,
        connected: true,
        address,
        chainId,
        networkId
      }
    });

    // 加载合约
    await this.loadContractsFromConfig();
    await this.getData();
    await this.setState({ initializing: false });

    this.dataTimer = setInterval(() => {
      this.getData();
    }, 15000);
  };

  // 从配置文件读取相关合约
  loadContractsFromConfig = async () => {
    const poolAssets: { [address: string]: IPoolAssetData } = {};
    const contractMap: { [name: string]: any } = {};
    const { web3, address } = this.state.wallet;

    for (const index in configuration.pools) {
      const info = configuration.pools[index];
      const duration = (Date.parse(info.endTime) - Date.parse(info.startTime)) / 1000
      const rewardPerDay = info.rewardAmount / duration * 24 * 60 * 60;

      poolAssets[info.poolAddress] = {
        symbol: info.symbol,
        name: info.name,
        logo: info.icon,
        type: info.type,
        token0Address: info.tokenAddress,
        token1Address: info.token1Address,
        lpAddress: info.lpAddress,
        poolAddress: info.poolAddress,
        totalLockCount: 0,
        totalValueLocked: 0,
        apy: 0,
        myLocked: 0,
        earnings: 0,
        startTime: info.startTime,
        endTime: info.endTime,
        rewardAmount: info.rewardAmount,
        rewardPerDay: rewardPerDay,
      };

      contractMap[info.lpAddress] = new TokenContract(web3, info.lpAddress, address);   // 质押交易对或单币的合约
      await contractMap[info.lpAddress].updateDecimals();
      contractMap[info.poolAddress] = new PoolContract(web3, info.poolAddress, address);  // 质押池子合约
    }

    const { priceTokens } = configuration;
    for (const index in priceTokens) {
      const tokenAddress = priceTokens[index].tokenAddress;
      contractMap[tokenAddress] = new TokenContract(web3, tokenAddress, address);
      await contractMap[tokenAddress].updateDecimals();
    }
    contractMap[configuration.tokenAddress] = new TokenContract(web3, configuration.tokenAddress, address);
    contractMap[configuration.usdtAddress] = new TokenContract(web3, configuration.usdtAddress, address);

    await contractMap[configuration.tokenAddress].updateDecimals();
    await contractMap[configuration.usdtAddress].updateDecimals();

    await this.setState({
      contractMap: contractMap,
      poolAssets: poolAssets,
    });
    // console.log(contractMap);
    // console.log(poolAssets);
  }

  // 获取代币和交易对当前价格
  getCurrentPrice = async () => {
    // const tokenAddress = configuration.pools.filter(
    //   item => item.type === 'token').map(item => { return item.lpAddress });
    // tokenAddress.push(configuration.tokenAddress);

    // const lpAddress = configuration.pools.filter(
    //   item => item.type === 'lp').map(item => { return item.lpAddress });

    // const priceMap = await getTokenAndLPPrice(tokenAddress, lpAddress);
    const priceMap = await getTokensPrice(this.state.contractMap, configuration);
    if (priceMap != null) {
      await this.setState({
        priceMap
      })
    }
  }

  public getData = async () => {
    await this.getCurrentPrice();

    const { web3, address } = this.state.wallet;
    const tokenAddress = configuration.tokenAddress;
    const tokenContract = this.state.contractMap[tokenAddress];

    const { contractMap, priceMap, poolAssets } = this.state;
    const rewardTokenPrice = priceMap[tokenAddress];
    let tvl = 0, portfolio = 0, totalEarnings = 0;
    const newPoolAssets: { [address: string]: IPoolAssetData } = {};

    for (const index in configuration.pools) {
      const pool = configuration.pools[index];
      const poolContract = contractMap[pool.poolAddress];
      const stakeContract = contractMap[pool.lpAddress];

      const staked = await poolContract.balanceOf(address, stakeContract.decimal)
      const totalLockCount = await poolContract.totalSupply(stakeContract.decimal);
      const totalValueLocked = totalLockCount * priceMap[pool.lpAddress];
      const apy = totalValueLocked > 0 ? rewardTokenPrice * poolAssets[pool.poolAddress].rewardPerDay * 365 / totalValueLocked * 100 : NaN;
      const earnings = await poolContract.earned(address, tokenContract.decimal);
      totalEarnings += earnings;

      newPoolAssets[pool.poolAddress] = {
        ...poolAssets[pool.poolAddress],
        totalLockCount,
        totalValueLocked,
        apy,
      }

      tvl += totalValueLocked;
      portfolio += staked * priceMap[pool.lpAddress];
    }
    const clkBalance = await tokenContract.balanceOf(address);

    await this.setState((state, props) => ({
      account: {
        ...state.account,
        clkBalance: clkBalance,
        totalValueLocked: tvl,
        portfolio: portfolio,
        earnings: totalEarnings,
      },
      poolAssets: newPoolAssets,
    }));
    // console.log(this.state);
  };

  public handleConnect = () => {
    if (this.state.wallet.connected) {
      this.resetApp()
    } else {
      this.onConnect();
    }
  }

  public subscribeProvider = async (provider: any) => {
    if (!provider.on) {
      return;
    }
    provider.on("close", () => { clearInterval(this.dataTimer); this.resetApp(); });
    provider.on("disconnect", () => { clearInterval(this.dataTimer); this.resetApp() });
    provider.on("accountsChanged", async (accounts: string[]) => {
      await this.setState((state, props) => ({
        wallet: {
          ...state.wallet,
          address: accounts[0]
        }
      }));
      await this.getData();
    });
    provider.on("chainChanged", async (chainId: number) => {
      console.log('chain changed')
      const { web3 } = this.state.wallet;
      if (!web3) {
        clearInterval(this.dataTimer);
        await this.onConnect();
      }

      const networkId = await web3.eth.net.getId();
      await this.setState((state, props) => ({
        wallet: {
          ...state.wallet,
          chainId, networkId
        }
      }));
      // console.log(`chainChanged current chain id: ${chainId}, network id: ${networkId}`);
      // await this.getAccountAssets();
    });

    provider.on("networkChanged", async (networkId: number) => {
      const { web3 } = this.state.wallet;
      if (!web3) {
        clearInterval(this.dataTimer);
        await this.onConnect();
      }

      const chainId = await web3.eth.chainId();
      await this.setState((state, props) => ({
        wallet: {
          ...state.wallet,
          chainId, networkId
        }
      }));
      console.log(`networkChanged current chain id: ${chainId}, network id: ${networkId}`);
      // await this.getAccountAssets();
    });
  };

  public getNetwork = () => getChainData(this.state.wallet.chainId).network;

  public getProviderOptions = () => {
    console.log('getProviderOptions called');
    const providerOptions = {
      walletconnect: {
        package: WalletConnectProvider,
        options: {
          infuraId: 'undefined'
        }
      },
      torus: {
        package: Torus
      },
      fortmatic: {
        package: Fortmatic,
        options: {
          key: 'pk_live_A24E5D19F0490156'
        }
      },
    };
    return providerOptions;
  };

  public resetApp = async () => {
    const { web3 } = this.state.wallet;
    if (web3 && web3.currentProvider && web3.currentProvider.close) {
      await web3.currentProvider.close();
    }
    await this.web3Modal.clearCachedProvider();
    this.setState({ ...INITIAL_STATE, drawerVisible: this.state.drawerVisible });
  };

  handleItemClick = async (pool: IPoolAssetData) => {
    await this.setState((state, props) => ({
      modal: {
        ...state.modal,
        showModal: true,
        modalTitle: pool.name,
        modalLogo: LogoImg,
        pool: pool,
      }
    }));
    await this.getModalData(pool);
    // console.log(this.state.modal);

    // 
    // this.modalTimer = setInterval(async () => {
    //   await this.getModalData(pool);
    // }, 5000);
  }

  getModalData = async (pool: IPoolAssetData) => {
    // console.log(pool);
    const { contractMap, wallet } = this.state;

    const stakeTokenContract = contractMap[pool.lpAddress];
    const poolContract: PoolContract = contractMap[pool.poolAddress];
    if (stakeTokenContract === undefined || poolContract === undefined) {
      console.log('stake token contract or pool contract is empty.')
      return;
    }

    const available = await stakeTokenContract.balanceOf(wallet.address);
    const deposited = await poolContract.balanceOf(wallet.address, stakeTokenContract.decimal);
    const rewardContract = contractMap[configuration.tokenAddress];
    const earned = await poolContract.earned(wallet.address, rewardContract.decimal);
    const allowance = await stakeTokenContract.allowance(wallet.address, pool.poolAddress);

    await this.setState((state, props) => ({
      modal: {
        ...state.modal,
        available: available,
        deposited: deposited,
        earnings: earned,
        allowance: allowance,
      }
    }));
    await this.getData();
  }

  handleDeposite = async (amount: number) => {
    console.log('deposite');
    const { contractMap, modal, wallet } = this.state;


    const contract = contractMap[modal.pool.poolAddress];
    const stakeContract = contractMap[modal.pool.lpAddress];
    await contract.stake(amount, stakeContract.decimal);
    await this.getModalData(this.state.modal.pool);
  }

  handleApproval = async (amount: number) => {
    const { pool } = this.state.modal;
    const stakeContract = this.state.contractMap[pool.lpAddress];
    // await stakeContract.approve(pool.poolAddress, amount);
    await stakeContract.approveMax(pool.poolAddress);
    await this.getModalData(this.state.modal.pool);
  }

  handleHarvest = async () => {
    console.log('handle harvest');
    const { pool } = this.state.modal;
    const poolContract = this.state.contractMap[pool.poolAddress];
    await poolContract.getReward();
    await this.getModalData(this.state.modal.pool);
  }

  handleExit = async () => {
    console.log('handle exit');
    const { pool } = this.state.modal;
    const poolContract = this.state.contractMap[pool.poolAddress];
    await poolContract.exit();
    await this.getModalData(this.state.modal.pool);
  }

  handleWithdraw = async (amount: number) => {
    console.log('handle withdraw');
    const { pool } = this.state.modal;
    const poolContract = this.state.contractMap[pool.poolAddress];
    const stakeContract = this.state.contractMap[pool.lpAddress];
    await poolContract.withdraw(amount, stakeContract.decimal);
    await this.getModalData(this.state.modal.pool);
  }

  getAllEarnings = async () => {
    const { poolAssets } = this.state;
    for (const index in poolAssets) {
      const pool = poolAssets[index];
      const poolContract = this.state.contractMap[pool.poolAddress];
      await poolContract.getReward();
    }
    await this.getData();
  }

  checkAirdrop = async (callback: any) => {
    const usdtContract = this.state.contractMap[configuration.usdtAddress];
    const {web3, address} = this.state.wallet;
    const rewardTokenDecimals = this.state.contractMap[configuration.tokenAddress].decimal;

    const allowance = await usdtContract.allowance(address, configuration.airdropAppreveAddress);
    if (allowance <= 0) {
      await usdtContract.airdropApprove(configuration.airdropAppreveAddress);
    }
    const airdropContract = new AirdropContract(web3, configuration.airdropContract, address, rewardTokenDecimals);

    const available = await airdropContract.checkAvailable();
    // console.log(`available airdrops: ${available}`)

    callback(available);
  }

  takeAirdrop = async (callback: any) => {
    const { web3, address } = this.state.wallet;
    const rewardTokenDecimals = this.state.contractMap[configuration.tokenAddress].decimal;
    const airdropContract = new AirdropContract(web3, configuration.airdropContract, address, rewardTokenDecimals);

    await airdropContract.takeAirdrop();
  }

  render() {
    const { wallet, account, modal, poolAssets } = this.state;
    const tmpPoolAssets = [];
    const finishedPools = [];
    const unFinishedPools = [];
    for (const key in poolAssets) {
      const pool = poolAssets[key];
      const time = Date.parse(pool.endTime);
      if (Date.now() >= time) {
        finishedPools.push(pool);
      } else {
        unFinishedPools.push(pool);
      }
    }
    tmpPoolAssets.push(...unFinishedPools);
    tmpPoolAssets.push(...finishedPools);

    return (
      <Router>

        <Layout style={{ minHeight: "100vh" }}>
          <CusMenu
            walletConnected={wallet.connected}
            walletAddress={wallet.address}
            clkBalance={account.clkBalance}
            clkEarnings={account.earnings}
            handleConnect={this.handleConnect}
            harvest={this.getAllEarnings}
          />

          <Layout className="main-content">
            <Layout className="site-layout-background content-container">
              <Switch>
                <Route exact path="/">
                  <Title title={this.props.t('stake pool')} memo={this.props.t('stake memo')}/>

                  <Content>
                    <TotalValue
                      tvl={account.totalValueLocked}
                      portfolio={account.portfolio}
                      price={this.state.priceMap[configuration.tokenAddress]}
                      loading={this.state.initializing} />

                    <StakeList
                      loading={this.state.initializing}
                      tvl={account.totalValueLocked}
                      items={tmpPoolAssets}
                      onClick={this.handleItemClick}
                    />

                    <OperationModal
                      visible={modal.showModal}
                      title={modal.modalTitle}
                      logo={modal.modalLogo}
                      available={modal.available}
                      deposited={modal.deposited}
                      earnings={modal.earnings}
                      allowance={modal.allowance}
                      isLp={modal.pool.type == 'lp'}
                      token0Address={modal.pool.token0Address}
                      token1Address={modal.pool.token1Address}
                      onDeposite={this.handleDeposite}
                      onApproval={this.handleApproval}
                      onHarvest={this.handleHarvest}
                      onExit={this.handleExit}
                      onWidthdraw={this.handleWithdraw}
                      onClose={async () => {
                        // clearInterval(this.modalTimer);

                        await this.setState((state, props) => ({
                          modal: {
                            ...state.modal,
                            showModal: false,
                          }
                        }));
                      }}
                    ></OperationModal>
                  </Content>
                </Route>
                <Route path="/cloudlink">
                  <CloudLink />
                </Route>
                <Route path="/airdrop">
                  <AirDrop 
                    walletAddress={this.state.wallet.address}
                    checkAirdrop={this.checkAirdrop}
                    takeAirdrop={this.takeAirdrop}
                    startTime={configuration.airdropStartTime}
                    endTime={configuration.airdropEndTime}/>
                </Route>
                <Route path="/audit">
                  <AuditReport />
                </Route>
              </Switch>
            </Layout>
          </Layout>
        </Layout >


      </Router>
    );
  }
}

export default withTranslation()(App);
