import React from 'react';
import styled from 'styled-components';
import MediaQuery from 'react-responsive'
import { Spin } from 'antd';
import { withTranslation } from 'react-i18next';
import StakeItem from './StakeItem';
import { IPoolAssetData } from "../helpers/types";

const Content = styled.div``;

const ItemContainer = styled.div`
    border-bottom: 1px solid #FFFFFF;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    padding: 0;
`;

const ItemTitle = styled.div`
    flex-grow: 0;
    max-width: 33.333333%;
    flex-basis: 33.333333%;
`;

const ItemContent = styled.div`
    flex-grow: 0;
    max-width: 16.666667%;
    flex-basis: 16.666667%;
`;

interface IStakeListProps {
    loading: boolean;
    tvl: number;
    items: IPoolAssetData[];
    onClick(pool: IPoolAssetData): void;
    t: any;
    i18n: any;
}

interface IStakeListState { }


class StakeList extends React.Component<IStakeListProps, IStakeListState> {
    constructor(props: IStakeListProps) {
        super(props);
    }    

    render() {
        const stakeList = this.props.items.map((item: IPoolAssetData) => {
            return <StakeItem
                key={item.symbol}
                loading={this.props.loading}
                logo={item.logo}
                title={item.symbol}
                tokens={item.totalLockCount}
                yearRoi={item.apy}
                value={item.totalValueLocked}
                startTime={item.startTime}
                endTime={item.endTime}
                onClick={() => { this.props.onClick(item) }}>
            </StakeItem>
        });

        return (
            <>
                <MediaQuery minWidth={1024}>
                    <ItemContainer style={{ marginTop: "2rem" }}>
                        <ItemTitle><p style={{ color: "#224168" }}>{this.props.t('all setts')} - <span style={{ color: "#0066CC"}}>$ {this.props.tvl}</span></p></ItemTitle>
                    </ItemContainer>
                    <ItemContainer style={{padding: "4px 16px"}}>
                        <ItemContent style={{ textAlign: "left", maxWidth: "18%", flexBasis: "18%" }}></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}><p className="stake-item" style={{ fontSize: "14px" }}>{this.props.t('locked')}</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}><p className="stake-item" style={{ fontSize: "14px" }}>{this.props.t('value')}</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "20%", flexBasis: "20%" }}><p className="stake-item" style={{ fontSize: "14px" }}>{this.props.t('apy')}</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "14%", flexBasis: "14%" }}><p className="stake-item" style={{ fontSize: "14px" }}>{this.props.t('status')}</p></ItemContent>
                    </ItemContainer>
                </MediaQuery>
                <MediaQuery maxWidth={1024}>
                    <Content style={{ marginTop: "2rem" }}></Content>
                    <ItemTitle className="item-title"><p style={{ color: "#224168" }}>{this.props.t('all setts')} - <span style={{ color: "#0066CC" }}>$ {this.props.tvl}</span></p></ItemTitle>
                </MediaQuery>

                {
                    this.props.loading ?
                        <div style={{ textAlign: "center", margin: "8px auto" }}><Spin></Spin></div> :
                        <div>{stakeList}</div>
                }
            </>
        );
    }
}

export default withTranslation()(StakeList);
