import React from 'react';
import { withTranslation } from 'react-i18next';
import { Button } from 'antd';
import styles from './AirDrop.module.css';
import Title from '../components/Title';

interface IAirdropProp {
    t: any;
    i18n: any;
    walletAddress: string;
    checkAirdrop: any;
    takeAirdrop: any;
    startTime: string;
    endTime: string;
}
interface IAirdropState {
    checkPending: boolean;
    takePending: boolean;
    available: boolean;
    availableAmount: number;
    status: string,
    time: string;
    airdropping: boolean;
}

class AirDrop extends React.Component<IAirdropProp, IAirdropState> {
    timer: any;

    constructor(props: IAirdropProp) {
        super(props);

        this.state = {
            checkPending: false,
            takePending: false,
            available: false,
            airdropping: false,
            availableAmount: 0,
            status: '',
            time: '',
        }
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            this.getAirdropStatus();
        }, 1000);
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }

    getAirdropStatus = async () => {
        console.log('get airdrop status')
        const now = Date.now();
        const startTime = Date.parse(this.props.startTime);
        const endTime = Date.parse(this.props.endTime);

        if (startTime > now) {
            await this.setState({
                status: this.props.t('airdrop not started'),
                time: this.getCountdownContent(startTime - now),
            });
        } else if (now > endTime) {
            await this.setState({
                status: this.props.t('airdrop finished'),
                time: '',
            })
        } else {
            await this.setState({
                status: this.props.t('airdrop running'),
                time: '',
                airdropping: true,
            })
        }
    }

    formatTimestamp = (num: number) => {
        return num.toString().padStart(2, '0');
    }

    getCountdownContent = (timestamp: number) => {
        const MINUTE_TOMESTAMP: number = 60;
        const HOUR_TIMESTAMP: number = 60 * MINUTE_TOMESTAMP;
        const DAY_TIMESTAMP: number = 24 * HOUR_TIMESTAMP;

        timestamp = Math.floor(timestamp / 1000);

        const day = Math.floor(timestamp / DAY_TIMESTAMP);
        timestamp %= DAY_TIMESTAMP;

        const hour = Math.floor(timestamp / HOUR_TIMESTAMP);
        timestamp %= HOUR_TIMESTAMP;

        const minute = Math.floor(timestamp / MINUTE_TOMESTAMP);
        timestamp %= MINUTE_TOMESTAMP;

        const second = Math.floor(timestamp);

        const record = `${this.formatTimestamp(day)}:${this.formatTimestamp(hour)}:${this.formatTimestamp(minute)}:${this.formatTimestamp(second)}`;
        return record;
    }

    checkAvailable = async () => {
        await this.setState((state, props) => ({
            checkPending: true,
        }));
        try {
            await this.props.checkAirdrop((amount: number)=>{
                this.setState({
                    availableAmount: amount,
                    available: true,
                })
            });
            
        } finally {
            await this.setState((state, props) => ({
                checkPending: false,
            }));
        }
    }

    takeAirdrop = async () => {
        await this.setState((state, props) => ({
            takePending: true,
        }));
        try {
            await this.props.takeAirdrop();
            this.setState({ availableAmount: 0 })
        } finally {
            await this.setState((state, props) => ({
                takePending: false,
            }));
        }
    }

    render() {
        return (
            <>
                <Title title={this.props.t('airdrop title')} memo={this.props.t('airdrop memo')} />

                <div className={styles.countdown}>
                    <h6>{this.props.t("airdrop status")}: {this.state.status} {this.state.time ? `(${this.state.time})` : ''}</h6>
                </div>
                
                <Button className={styles['action-button']} loading={this.state.checkPending}
                    onClick={this.checkAvailable}
                    disabled={this.state.checkPending || !this.state.airdropping}>{this.props.t('airdrop checking')}</Button>

                {
                    this.state.available
                        ? <p className={styles.text}>{this.props.t('airdrop available')}: <span className={styles.amount}>{this.state.availableAmount}</span>CLK</p>
                        : <p className={styles.text}></p>
                }

                {
                    this.state.available
                        ? <div className={styles["airdrop-container"]}>
                            <p>{this.props.t("wallet address")}: </p>
                            <p className={styles.address}>{this.props.walletAddress}</p>
                            <Button className={styles['action-button']} loading={this.state.takePending}
                                onClick={this.takeAirdrop}
                                disabled={this.state.availableAmount <= 0}>{this.props.t('take airdrop')}</Button>
                        </div>
                        : <div></div>
                }
            </>
        );
    }
}
export default withTranslation()(AirDrop);
