import React from 'react';
import MediaQuery from 'react-responsive'
import styled from 'styled-components';


const Content = styled.div``;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
`;

interface ITitleProp {
    title: string;
    memo: string;
    [field:string]: string;
}

class Title extends React.Component<ITitleProp> {
    constructor(props: ITitleProp) {
        super(props);
    }

    render() {
        const title = <>
            <div style={{ color: "#000", marginBottom: "2rem" }}>
                <h1 style={{ color: '#224168' }}>{this.props.title}</h1>
                <h6 style={{ color: '#224168B3' }}>{this.props.memo}</h6>
            </div>
        </>;

        return (
            <Content>
                <MediaQuery minWidth={1024}>
                    <Row>
                        {title}
                    </Row>
                </MediaQuery>

                <MediaQuery maxWidth={1024}>
                    <Column>
                        {title}
                    </Column>
                </MediaQuery>

            </Content>
        );
    }
}

export default Title;
