import styled from 'styled-components';
import MediaQuery from 'react-responsive'
import { withTranslation } from 'react-i18next';
import NumberCard from './NumberCard';

const Content = styled.div``;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
`;

interface ITotalValueProps {
    tvl: number;
    portfolio: number;
    price: number;
    loading: boolean;
    t: any;
    i18n: any;
}

function TotalValue(props: ITotalValueProps) {
    return (
        <>
            <MediaQuery minWidth={1024}>
                <Row>
                    <NumberCard firstValue={props.t('total value locked')} secondValue={props.tvl} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue={props.t('your locked value')}  secondValue={props.portfolio} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue={props.t('clk price')}  secondValue={props.price} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                </Row>
            </MediaQuery>

            <MediaQuery maxWidth={1024}>
                <Column>
                    <NumberCard firstValue={props.t('total value locked')} secondValue={props.tvl} style={{ width: "100%", marginTop: "2rem" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue={props.t('your locked value')} secondValue={props.portfolio} style={{ width: "100%", marginTop: "0.5rem" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue={props.t('clk price')} secondValue={props.price} style={{ width: "100%", marginTop: "0.5rem" }} loading={props.loading}></NumberCard>
                </Column>
            </MediaQuery>
        </>
    );
}

export default withTranslation()(TotalValue);